	// ];
    export default{
        name:'Webdesign',
        data(){
            return{
                webdesignData:[]
            }
        },
        mounted(){
        	var _this=this;
        	var url='./static/js/webdesign.data.js';
        	$.ajax({
				url:url,
				type:'GET',
				dataType:'json'
			}).done(res=>{
				_this.webdesignData=res;
			}).fail(()=>{
				console.log('fail')
			})
        }
    }
</script>
<style lang="scss" scope>
$yellow_color:#FFF100;
.main{
	flex:1 0 auto;
	padding-top: 20px;
	.main-box{
		display:flex;
		flex-wrap:wrap;
	}
	.my-box{
		.thumbnail{
			padding: 6px;
			border-radius: 0;
			border: none;
			.thumbnail-box{
				position: relative;
				overflow: hidden;
				.thumbnail-img{
					transition:all .3s;
				}
				.caption-box{
					width: 100%;
					height: 100%;
					padding: 14px;
					background-color: rgba(0,0,0,.5);
					position: absolute;
					top: 0;
					left: 0;
					color:#fff;
					opacity: 0;
					transition:all .5s;
					display: flex;
					align-items:center;
					.thumbnail-link{
						background-color: $yellow_color;
						color:#000;
						padding: .1rem 1rem;
						text-decoration: none;
						display: inline-block;
						margin-top: 2px;
					}
				}
				&:hover{
					.thumbnail-img{
						transform:scale(1.1);
					}
					.caption{
						opacity: 1;
					}
				}
			}
		}
	}
}	
</style>


