<template>
    <div class="carousel-box hidden-xs">
		<div class="container">
			<div class="row">
				<div class="col-md-8 col-md-offset-2 col-sm-8 col-sm-offset-2">
					<div class="user-carousel">
						<div id="carousel" class="carousel slide" data-ride="caroudel">
							<ol class="carousel-indicators">
								<li data-target="#carousel"
									v-for="(caoruDate,i) in caourselData"
									v-bind:data-slide-to="i"
									v-bind:class="i==0?'active':''"></li>
							</ol>

							<div class="carousel-inner">
								<div class="item" v-for="(caoruDate,i) in caourselData" v-bind:class="i==0?'active':''">
									<a v-bind:href="caoruDate.href" target="_blank">
										<img v-bind:src="caoruDate.img" alt="">
									</a>
								</div>
							</div>

							<a class="left carousel-control" href="#carousel" role="button" data-slide="prev">◀</a>
							<a class="right carousel-control" href="#carousel" role="button" data-slide="next">▶</a>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
    var caourselData=[
		{'href':'javascipt:;','img':'./static/images/carousel-a.jpg'},
		{'href':'javascipt:;','img':'./static/images/carousel-b.jpg'}
	]
    export default{
        name:'Carousel',
        data(){
            return{
                caourselData:caourselData
            }
        }
    }
</script>
<style lang="scss" scope>
$yellow_color:#FFF100;
.carousel-box{
	background-image: url('../../static/images/bg.jpg');
}
.carousel-indicators{
	li.active{
		margin-right: 6px;
		background-color: $yellow_color;
		border:none;
	}
	li{
		margin-right: 6px;
	}
}
.carousel-control{
	top: 50%;
	opacity: 1;
	width: 36px;
	height: 36px;
	line-height: 36px;
	font-size: 36px;
	border-radius: 50%;
	text-shadow:none;
	color:$yellow_color;
	font-size: 64px;
	&:hover{
		color:$yellow_color;
		opacity: 1;
	}
	&:focus{
		color:$yellow_color;
		opacity: 1;
	}
	&.left{
		background-color: transparent;
		left: 0;
		transform:translate(-200%,-50%);
		background-image: none;
	}
	&.right{
		background-color: transparent;
		right: 0;
		transform:translate(200%,-50%);
		background-image: none;
	}
}

@media (min-width:768px){
	.carousel-box{
		width: 100%;
		background-position: center 80%;
		background-attachment: fixed;
		background-color: #393939;
		position: relative;
		&:before{
			content:'';
			display:block;
			width:100%;
			height:100%;
			background-image: url('../../static/images/black.png');
			opacity: .5;
			position: absolute;
			top:0;
			left:0;
		}
		.user-carousel{
			padding: 18px;
		}
	}
}

</style>


